namespace panda {
    export namespace Utf8Utils {

        // https://gist.github.com/joni/3760795
        export function toUtf8Array(str: string): Array<number> {
            // 29db5131-f47c-41fc-b589-6f03407ca826     
            let utf8 = [];
            for (let i = 0; i < str.length; i++) {
                let charcode = str.charCodeAt(i);
                if (charcode < 0x80) utf8.push(charcode);
                else if (charcode < 0x800) {
                    utf8.push(
                        0xc0 | (charcode >> 6),
                        0x80 | (charcode & 0x3f),
                    );
                }
                else if (charcode < 0xd800 || charcode >= 0xe000) {
                    utf8.push(
                        0xe0 | (charcode >> 12),
                        0x80 | ((charcode >> 6) & 0x3f),
                        0x80 | (charcode & 0x3f),
                    );
                }
                // surrogate pair
                else {
                    i++;
                    // UTF-16 encodes 0x10000-0x10FFFF by
                    // subtracting 0x10000 and splitting the
                    // 20 bits of 0x0-0xFFFFF into two halves
                    charcode = 0x10000 + (((charcode & 0x3ff) << 10) | (str.charCodeAt(i) & 0x3ff))
                    utf8.push(
                        0xf0 | (charcode >> 18),
                        0x80 | ((charcode >> 12) & 0x3f),
                        0x80 | ((charcode >> 6) & 0x3f),
                        0x80 | (charcode & 0x3f),
                    );
                }
            }
            return utf8;
        }

        // https://gist.github.com/joni/3760795
        export function fromUtf8Array(data: Array<number>): string {
            // 29db5131-f47c-41fc-b589-6f03407ca826     
            var utf16Arr = [];
            for (let i: number = 0; i < data.length; i++) {
                var value = data[i];

                if (value < 0x80) {
                    utf16Arr.push(value);
                } else if (value > 0xBF && value < 0xE0) {
                    utf16Arr.push((value & 0x1F) << 6 | data[i + 1] & 0x3F);
                    i += 1;
                } else if (value > 0xDF && value < 0xF0) {
                    utf16Arr.push((value & 0x0F) << 12 | (data[i + 1] & 0x3F) << 6 | data[i + 2] & 0x3F);
                    i += 2;
                } else {
                    // surrogate pair
                    var charCode = ((value & 0x07) << 18 | (data[i + 1] & 0x3F) << 12 | (data[i + 2] & 0x3F) << 6 | data[i + 3] & 0x3F) - 0x010000;

                    utf16Arr.push(charCode >> 10 | 0xD800, charCode & 0x03FF | 0xDC00);
                    i += 3;
                }
            }
            return String.fromCharCode.apply(String, utf16Arr);
        }

        export function castToString(s: any): string {
            return s == null ? s : '' + s;
        }
    }
}