namespace panda {
	export class Console {
		public static $INIT(): void {
			let origConsoleLog = console.log;

			console.log = function (...args: Array<any>) {
				Console.logDebug('console.log', ...args);
			}
			console.warn = function (...args: Array<any>) {
				Console.logWarn('console.warn', ...args);
			}
			console.error = function (...args: Array<any>) {
				Console.logError('console.error', ...args);
			}
			console.exception = function (...args: Array<any>) {
				Console.logError('console.exception', ...args);
			}
			console.assert = function (assert: any, ...args: Array<any>) {
				if (!assert) {
					Console.logWarn('console.assert failed', ...args);
				} else {
					Console.logDebug('console.assert ok', ...args);
				}
			}

			Console.onErrorUncritical = new Signal<(error: Error | AdvancedError) => void>();
			Console.onLog = new Signal<(level: ConsoleMessageLevel, message: string, args: Array<any>) => void>();

			GlobalErrorListener.onGlobalError.addListener(Console.handler_unhaltedError);
			this.onErrorUncritical.addListener(function PandaConsole_handler_errorUncritical(error: Error | AdvancedError): void {
				Console.logWarn('' + error);
				let stack: string = AdvancedError.getStacktrace(error);
				if (stack) {
					Console.logWarn(stack);
				}
			});

			function colorConsoleWriter(level: ConsoleMessageLevel, message: string, args: Array<any>): void {
				// let argsCopy = args.slice();
				// if (PandaConsoleMessageStyles.hasStyle(level)) {
				// 	argsCopy.unshift('%c ' + message, PandaConsoleMessageStyles.getStyle(level));
				// } else {
				// 	argsCopy.unshift(message);
				// }
				// origConsoleLog.apply(console, argsCopy);
				let argsArray = [message].concat(args);
				if (Console.filterFunc instanceof Function) {
					if (!Console.filterFunc.apply(null, argsArray)) {
						return;
					}
				}
				origConsoleLog.apply(console, argsArray);
			}

			function normalConsoleWriter(level: ConsoleMessageLevel, message: string, args: Array<any>): void {
				// let argsCopy = args.slice();
				// argsCopy.unshift(message);
				// origConsoleLog.apply(console, argsCopy);
				
				let argsArray = [message].concat(args);
				if (Console.filterFunc instanceof Function) {
					if (!Console.filterFunc.apply(null, argsArray)) {
						return;
					}
				}
				origConsoleLog.apply(console, argsArray);
			}

			function stupidConsoleWriter(level: ConsoleMessageLevel, message: string, args: Array<any>): void {
				if (level === ConsoleMessageLevels.WARN) {
					//debugger;
				}
				let argsArray = [message].concat(args);
				if (Console.filterFunc instanceof Function) {
					if (!Console.filterFunc.apply(null, argsArray)) {
						return;
					}
				}
				origConsoleLog(level + ' ' + message + ' ' + args.join(' '));
			}

			if (typeof console === "undefined" || typeof origConsoleLog === "undefined") {
				// skip logging to console
			} else {
				if (origConsoleLog) {
					if (origConsoleLog.apply) {
						if (this.colorsSupport()) {
							this.onLog.addListener(colorConsoleWriter);
						} else {
							this.onLog.addListener(normalConsoleWriter);
						}
					} else {
						this.onLog.addListener(stupidConsoleWriter);
					}
				}
			}
		}
		
		public static logExternal(message: string, ...args: Array<any>): void {
			this.onLog.emit(ConsoleMessageLevels.EXTERNAL, message, args);
		}

		public static logInfo(message: string, ...args: Array<any>): void {
			this.onLog.emit(ConsoleMessageLevels.INFO, message, args);
		}

		public static logDebug(message: string, ...args: Array<any>): void {
			this.onLog.emit(ConsoleMessageLevels.DEBUG, message, args);
		}

		public static logWarn(message: string, ...args: Array<any>): void {
			if ((window as any).noWebGlDetected) {
				return;
			}
			this.onLog.emit(ConsoleMessageLevels.WARN, message, args);
		}

		public static logError(message: string, ...args: Array<any>): void {
			if ((window as any).noWebGlDetected) {
				return;
			}
			this.onLog.emit(ConsoleMessageLevels.ERROR, message, args);
		}

		public static errorUncritical(error: Error | AdvancedError): void {
			if ((window as any).noWebGlDetected) {
				return;
			}
			this.onErrorUncritical.emit(error);
		}

		public static here: boolean = true;

		public static onErrorUncritical: Signal<(error: Error | AdvancedError) => void> = null;
		public static onLog: Signal<(level: ConsoleMessageLevel, message: string, args: Array<any>) => void> = null;
		public static filterFunc: (...args: any[]) => boolean;

		private static handler_unhaltedError(msg: string, url: string, line: number, col: number, error: any): void {
			Console.logError(msg, url, line, col, AdvancedError.getStacktrace(error));
		}

		private static _colorsSupport: boolean = null;
		private static colorsSupport(): boolean {
			if (this._colorsSupport == null) {
				this._colorsSupport = BrowserSpecific.colorsInConsole();
			}
			return this._colorsSupport;
		}
	}
}
