namespace panda {
	export namespace UniqIdGenerator {
		let countersMap = new Map<any, number>();
		export function getUniq(reason: any = null): number {
			let count = countersMap.get(reason);
			if (count === undefined) {
				countersMap.set(reason, 0);
				return 0;
			} else {
				let result = count + 1;
				countersMap.set(reason, result);
				return result;
			}
		}
	}
}