
namespace panda {
    export const enum Ordering {
        Less = -1,
        Equal = 0,
        Greater = 1,
    }

    export interface IDigits {
        readonly radix: number;
        readonly data: ReadonlyArray<number>;
        compare(other: IDigits): Ordering;
        toString(): string;
    }

    export class Digits {
        private static __pow2__pows = new Map<number, Array<Digits>>();
        public static pow2(radix: number, pow: number): IDigits {
            if (!Digits.__pow2__pows.has(radix)) {
                const one = Digits.radixToPrefix(radix) + '1';
                Digits.__pow2__pows.set(radix, [new Digits(one)]);
            }

            const pows = Digits.__pow2__pows.get(radix);
            for (let i = pows.length; i <= pow; ++i) {
                const prevPow2 = pows[i - 1];
                const pow2 = new Digits(prevPow2);
                pow2.mul2();
                pows.push(pow2);
            }

            return pows[pow];
        }

        _radix: number;
        _data: Array<number>;

        public get radix(): number { return this._radix; }

        public get data(): ReadonlyArray<number> { return this._data; }

        constructor(value: number | IDigits | string = '0') {
            this._radix = 10;
            this._data = [];
            if (typeof value === 'number') {
                this.setFromNumber(value);
            }
            else if (typeof value === 'string') {
                this.setFromString(value);
            }
            else {
                this.setFromDigits(value);
            }
        }

        public setFromDigits(other: IDigits): void {
            this._radix = other.radix;
            this._data.length = 0;
            this._data.push(...other.data);
        }

        public setFromNumber(value: number): void {
            if (value < 0) throw new Error('negative values are not supported');

            this.setToZero(10);

            while (value > 0) {
                let digit = value % 10;
                this._data.push(digit);

                value = (value - digit) / 10;
            }
        }

        public setFromString(value: string = '0'): void {
            this._radix = Digits.parseRadix(value);
            if (this._radix !== 10) {
                value = value.slice(2);
            }

            this._data.length = 0;
            for (let i = value.length - 1; i >= 0; --i) {
                let digit = Digits.charToDigit(value.charCodeAt(i));
                if (digit >= this._radix) {
                    throw new Error(`digit ${digit} bigger than radix ${this._radix}`);
                }
                this._data.push(digit);
            }

            this.shrink();
        }

        private static __setBitFields__digits = new Digits();
        public setFromBitFields(fieldSizes: Array<number>, fields: Array<number>): void {
            this.setToZero(2);

            let digits = Digits.__setBitFields__digits;
            for (let i = 0; i < fields.length; ++i) {
                let field = fields[i];
                let fieldSize = fieldSizes[i];

                digits.setFromNumber(field);
                digits.setRadix(2);
                for (let j = 0; j < fieldSize; ++j) {
                    let bit = j < digits._data.length ? digits._data[j] : 0;
                    this._data.push(bit);
                }
            }

            this.shrink();
        }

        private static __readBitFields__digits = new Digits();
        public readBitFields(fieldSizes: Array<number>): Array<number> {
            let digits: Digits = this;
            if (this._radix != 2) {
                digits = Digits.__readBitFields__digits;
                digits.setFromDigits(this);
                digits.setRadix(2);
            }

            let fields = [];
            let offset = 0;
            for (let size of fieldSizes) {
                let field = 0;
                for (let pow = 0; pow < size; ++pow) {
                    let bit = offset < digits._data.length ? digits._data[offset] : 0;
                    if (bit !== 0) {
                        field += Math.pow(2, pow);
                    }

                    ++offset;
                }
                fields.push(field);
            }

            return fields;
        }

        public setToZero(radix: number = -1): void {
            if (radix !== -1) {
                Digits.radixToPrefix(radix);
                this._radix = radix;
            }
            this._data.length = 0;
        }

        private static __convertToRadix__digits = new Digits();
        public setRadix(radix_: number | string): void {
            const radix = typeof radix_ === 'number' ? radix_ : Digits.parseRadix(radix_);
            if (radix === this._radix) return;
            Digits.radixToPrefix(radix);

            if (radix === 2) {
                let bitsCount = 0;
                while (true) {
                    const nextPow = bitsCount;
                    const nextPow2 = Digits.pow2(this._radix, nextPow);
                    if (this.compare(nextPow2) === Ordering.Less) {
                        break;
                    }

                    ++bitsCount;
                }

                let digits = Digits.__convertToRadix__digits;
                digits.setFromDigits(this);

                this.setToZero(2);

                for (let pow = bitsCount; pow >= 0; --pow) {
                    let pow2 = Digits.pow2(digits._radix, pow);
                    if (pow2.compare(digits) === Ordering.Greater) {
                        this._data.push(0);
                    }
                    else {
                        this._data.push(1);
                        digits.sub(pow2);
                    }
                }
                this._data.reverse();

                this.shrink();
            }
            else {
                this.setRadix(2);

                let digits = Digits.__convertToRadix__digits;
                digits.setToZero(radix);

                for (let pow = 0; pow < this._data.length; ++pow) {
                    const bit = this._data[pow];
                    if (bit === 1) {
                        const pow2 = Digits.pow2(radix, pow);
                        digits.add(pow2);
                    }
                }

                this.setFromDigits(digits);
            }
        }

        private static __compare__digits = new Digits();
        public compare(other: IDigits): Ordering {
            if (this._radix !== other.radix) {
                Digits.__compare__digits.setFromDigits(other);
                Digits.__compare__digits.setRadix(this._radix);
                other = Digits.__compare__digits;
            }

            if (this._data.length !== other.data.length) {
                return this._data.length > other.data.length ? Ordering.Greater : Ordering.Less;
            }

            for (let i = this._data.length - 1; i >= 0; --i) {
                let a = this._data[i];
                let b = other.data[i];
                if (a < b) return Ordering.Less;
                if (a > b) return Ordering.Greater;
            }
            return Ordering.Equal;
        }

        private static __add__digits = new Digits();
        public add(other: IDigits): void {
            if (this._radix !== other.radix) {
                Digits.__add__digits.setFromDigits(other);
                Digits.__add__digits.setRadix(this._radix);
                other = Digits.__add__digits;
            }

            const maxLen = Math.max(this._data.length, other.data.length);

            let t = 0;
            for (let i = 0; i < maxLen; ++i) {
                let a = i < this._data.length ? this._data[i] : 0;
                let b = i < other.data.length ? other.data[i] : 0;

                let r = -this._radix + a + b + t;
                if (r < 0) {
                    r += this._radix;
                    t = 0;
                }
                else {
                    t = 1;
                }

                this._data[i] = r;
            }
            if (t != 0) {
                this._data.push(t);
            }
        }

        private static __sub__digits = new Digits();
        public sub(other: IDigits): void {
            if (this._radix !== other.radix) {
                Digits.__sub__digits.setFromDigits(other);
                Digits.__sub__digits.setRadix(this._radix);
                other = Digits.__sub__digits;
            }

            let cmp = this.compare(other);
            if (cmp === Ordering.Less) throw new Error('cant subtract a larger value');
            if (cmp === Ordering.Equal) {
                this.setToZero();
                return;
            }

            let t = 0;
            for (let i = 0; i < this._data.length; ++i) {
                let a = this._data[i];
                let b = i < other.data.length ? other.data[i] : 0;

                let r = a - b + t;
                if (r < 0) {
                    r += this._radix;
                    t = -1;
                }
                else {
                    t = 0;
                }

                this._data[i] = r;
            }

            this.shrink();
        }

        public mul2(): void {
            let t = 0;
            for (let i = 0; i < this._data.length; ++i) {
                let a = this._data[i];

                let r = -this._radix + a + a + t;
                if (r < 0) {
                    r += this._radix;
                    t = 0;
                }
                else {
                    t = 1;
                }

                this._data[i] = r;
            }
            if (t != 0) {
                this._data.push(t);
            }
        }

        private shrink(): void {
            let lastSignificantDigit = this._data.length - 1;
            while (lastSignificantDigit >= 0 && this._data[lastSignificantDigit] === 0) {
                --lastSignificantDigit;
            }
            this._data.length = lastSignificantDigit + 1;
        }

        public toString() {
            if (this._data.length === 0) {
                return Digits.radixToPrefix(this._radix) + '0';
            }

            let str = '';
            for (const digit of this._data) {
                str = Digits.digitToChar(digit) + str;
            }
            return Digits.radixToPrefix(this._radix) + str;
        }

        public static codeOf(char: string): number {
            return char.charCodeAt(0);
        }

        public static parseRadix(value: string): number {
            if (value.charCodeAt(0) === Digits.codeOf('0')) {
                switch (value.charCodeAt(1)) {
                    case Digits.codeOf('K'): return 1000000000000000;
                    case Digits.codeOf('x'): return 16;
                    case Digits.codeOf('o'): return 8;
                    case Digits.codeOf('b'): return 2;
                }
            }
            return 10;
        }

        public static radixToPrefix(radix: number): string {
            switch (radix) {
                case 1000000000000000: return '0K';
                case 16: return '0x';
                case 10: return '';
                case 8: return '0o';
                case 2: return '0b';
            }
            throw new Error(`unsupported radix: ${radix}`);
        }

        public static charToDigit(char: number): number {
            switch (char) {
                case Digits.codeOf('0'): return 0;
                case Digits.codeOf('1'): return 1;
                case Digits.codeOf('2'): return 2;
                case Digits.codeOf('3'): return 3;
                case Digits.codeOf('4'): return 4;
                case Digits.codeOf('5'): return 5;
                case Digits.codeOf('6'): return 6;
                case Digits.codeOf('7'): return 7;
                case Digits.codeOf('8'): return 8;
                case Digits.codeOf('9'): return 9;
                case Digits.codeOf('a'):
                case Digits.codeOf('A'): return 10;
                case Digits.codeOf('b'):
                case Digits.codeOf('B'): return 11;
                case Digits.codeOf('c'):
                case Digits.codeOf('C'): return 12;
                case Digits.codeOf('d'):
                case Digits.codeOf('D'): return 13;
                case Digits.codeOf('e'):
                case Digits.codeOf('E'): return 14;
                case Digits.codeOf('f'):
                case Digits.codeOf('F'): return 15;
            }
            throw new Error(`unknown character of digit: ${char}`);
        }

        public static digitToChar(digit: number): string {
            switch (digit) {
                case 0: return '0';
                case 1: return '1';
                case 2: return '2';
                case 3: return '3';
                case 4: return '4';
                case 5: return '5';
                case 6: return '6';
                case 7: return '7';
                case 8: return '8';
                case 9: return '9';
                case 10: return 'A';
                case 11: return 'B';
                case 12: return 'C';
                case 13: return 'D';
                case 14: return 'E';
                case 15: return 'F';
            }
            throw new Error(`unsupported digit: ${digit}`);
        }
    }
}