namespace panda {
	export class WebGlDescribingStatEvent extends PlatformDescribingStatEvent {
		constructor(projectName: string, siteName: string, eventType: string = null) {
			super(projectName, siteName, eventType);
			let glInfo = PlatformDescriber.getInfo().webGLInfo;

			this.addUniq('max_vertex_attributes', glInfo.maxVertexAttributes, StatDataType.i32);
			this.addUniq('max_vertex_uniform_vectors', glInfo.maxVertexUniformVectors, StatDataType.i32);
			this.addUniq('max_vertex_texture_image_units', glInfo.maxVertexTextureImageUnits, StatDataType.i32);
			this.addUniq('max_varying_vectors', glInfo.maxVaryingVectors, StatDataType.i32);

			this.addUniq('aliased_line_width_range_0', glInfo.aliasedLineWidthRange[0], StatDataType.i32);
			this.addUniq('aliased_line_width_range_1', glInfo.aliasedLineWidthRange[1], StatDataType.i32);

			this.addUniq('aliased_point_size_range_0', glInfo.aliasedPointSizeRange[0], StatDataType.i32);
			this.addUniq('aliased_point_size_range_1', glInfo.aliasedPointSizeRange[1], StatDataType.i32);

			this.addUniq('max_fragment_uniform_vectors', glInfo.maxFragmentUniformVectors, StatDataType.i32);
			this.addUniq('max_texture_image_units', glInfo.maxTextureImageUnits, StatDataType.i32);

			this.addUniq('red_bits', glInfo.redBits, StatDataType.i8);
			this.addUniq('green_bits', glInfo.greenBits, StatDataType.i8);
			this.addUniq('blue_bits', glInfo.blueBits, StatDataType.i8);
			this.addUniq('alpha_bits', glInfo.alphaBits, StatDataType.i8);
			this.addUniq('depth_bits', glInfo.depthBits, StatDataType.i8);
			this.addUniq('stencil_bits', glInfo.stencilBits, StatDataType.i8);

			this.addUniq('max_renderbuffer_size', glInfo.maxRenderbufferSize, StatDataType.i32);
			this.addUniq('max_viewport_dims_0', glInfo.maxViewportDims[0], StatDataType.i32);
			this.addUniq('max_viewport_dims_1', glInfo.maxViewportDims[1], StatDataType.i32);
			this.addUniq('max_texture_size', glInfo.maxTextureSize, StatDataType.i32);
			this.addUniq('max_cube_map_texture_size', glInfo.maxCubeMapTextureSize, StatDataType.i32);
			this.addUniq('max_combined_texture_image_units', glInfo.maxCombinedTextureImageUnits, StatDataType.i32);
			this.addUniq('major_performance_caveat', glInfo.majorPerformanceCaveat, StatDataType.projDict8);

			let context = WebGLUtils.getDummyContext();
			this.addUniq('angle_instanced_arrays', WebGLUtils.getExtension(context, WebGLUtils.extensions.ANGLE_instanced_arrays) ? 'true' : 'false', StatDataType.projDict8);
			this.addUniq('oes_texture_float', WebGLUtils.getExtension(context, WebGLUtils.extensions.OES_texture_float) ? 'true' : 'false', StatDataType.projDict8);
			this.addUniq('oes_texture_float_linear', WebGLUtils.getExtension(context, WebGLUtils.extensions.OES_texture_float_linear) ? 'true' : 'false', StatDataType.projDict8);
			this.addUniq('ext_texture_filter_anisotropic', WebGLUtils.getExtension(context, WebGLUtils.extensions.EXT_texture_filter_anisotropic) ? 'true' : 'false', StatDataType.projDict8);
			this.addUniq('oes_standard_derivatives', WebGLUtils.getExtension(context, WebGLUtils.extensions.OES_standard_derivatives) ? 'true' : 'false', StatDataType.projDict8);
			this.addUniq('oes_element_index_uint', WebGLUtils.getExtension(context, WebGLUtils.extensions.OES_element_index_uint) ? 'true' : 'false', StatDataType.projDict8);
			this.addUniq('webgl_debug_renderer_info', WebGLUtils.getExtension(context, WebGLUtils.optionalExtensions.WEBGL_debug_renderer_info) ? 'true' : 'false', StatDataType.projDict8);
			this.addUniq('webgl_compressed_texture_s3tc', WebGLUtils.getExtension(context, WebGLUtils.optionalExtensions.WEBGL_compressed_texture_s3tc) ? 'true' : 'false', StatDataType.projDict8);
			this.addUniq('webgl_compressed_texture_pvrtc', WebGLUtils.getExtension(context, WebGLUtils.optionalExtensions.WEBGL_compressed_texture_pvrtc) ? 'true' : 'false', StatDataType.projDict8);
			this.addUniq('webgl_lose_context', WebGLUtils.getExtension(context, WebGLUtils.optionalExtensions.WEBGL_lose_context) ? 'true' : 'false', StatDataType.projDict8);
			this.addUniq('webgl_depth_texture', WebGLUtils.getExtension(context, WebGLUtils.optionalExtensions.WEBGL_depth_texture) ? 'true' : 'false', StatDataType.projDict8);
			this.addUniq('ext_frag_depth', WebGLUtils.getExtension(context, WebGLUtils.optionalExtensions.EXT_frag_depth) ? 'true' : 'false', StatDataType.projDict8);
		}
	}
}