class BuildInfo {
	// get values form BuildProperties in exists
	public static get buildDate(): string { return this.getPropByName('buildDate', '0')}
	public static get buildNumber(): string { return this.getPropByName('buildNumber', '0')}
	public static get online(): string { return this.getPropByName('online', 'true')}

	private static getPropByName(name: string, defaultValue: string) {
		let buildProps: any = (window as any)['BuildProperties'];
		return buildProps ? buildProps[name] : defaultValue;
	}
}
