namespace panda {
	export class NewSignal<T extends Function = () => void>{
		public constructor(name: string = 'NewSignal') {
			this._name = name;
		}
		private _name: string;
		private _listeners: Array<SignalNode<T> | null> | null = null;
		private _inRun: boolean = false;
		private _addQueue: Array<SignalNode<T> | null> | null = null; // may contain duplicates

		protected createListenersList(): void {
			this._listeners = new Array();
		}

		public addListener(listener: T, priority: number = 0): T {
			if (this._inRun) {
				if (!this._addQueue) {
					this._addQueue = new Array();
				}
				this._addQueue.push(new SignalNode(listener, priority));
			} else {
				this.addNode(listener, priority);
			}
			return listener;
		}

		private addNode(node: SignalNode<T>): T
		private addNode(listener: T, priority: number): T
		private addNode(arg1: any, arg2?: any): T {
			let listener: T = null;
			let node: SignalNode<T> = null;
			let priority: number = 0;
			if (arg1 instanceof SignalNode) {
				node = arg1;
				listener = node.listener;
				priority = node.priority;
			} else {
				listener = arg1;
				priority = arg2;
			}
			if (!this._listeners) {
				this.createListenersList();
			}
			if (this.hasListener(listener)) {
				return listener;
			}
			let i = this._listeners.length - 1;
			node = node || new SignalNode(listener, priority);
			while (i >= 0) { // todo: binary search. But backward linear search is the fastest way when 0 priority used.
				let cur = this._listeners[i];
				if (!cur || cur.priority < priority) {
					i--;
					continue;
				}
				if (cur.priority >= priority) {
					this._listeners.splice(i + 1, 0, node); // todo: check next and fill if null
					break;
				}
			}
			if (i < 0) {
				this._listeners.unshift(node);
			}
			return listener;
		}

		public hasListener(listener: T): boolean {
			if (this._listeners) {
				for (let node of this._listeners) {
					if (node && node.listener == listener) {
						return true;
					}
				}
			}
			if (this._addQueue) {
				for (let node of this._addQueue) {
					if (node && node.listener == listener) {
						return true;
					}
				}
			}
			return false;
		}

		public addListenerOnce(listener: T, priority: number = 0): T {
			let fakeListener: any = (...args: Array<any>) => {
				listener.apply(null, args);
				this.removeListener(fakeListener);
			}
			this.addListener(fakeListener as T, priority);
			return fakeListener;
		}

		public removeListener(listener: T): void {
			if (this._listeners) {
				for (let i = 0; i < this._listeners.length; i++) {
					let node = this._listeners[i];
					if (node && node.listener == listener) {
						this._listeners[i] = null;
						break;
					}
				}
			}
			if (this._addQueue) {
				for (let i = 0; i < this._addQueue.length; i++) {
					let node = this._addQueue[i];
					if (node && node.listener == listener) {
						this._addQueue[i] = null;
						// without break, because here may be duplicates
					}
				}
			}
		}

		public emit: T;
		/**
		 * alias for emit
		 * @type {any}
		 */
		public dispatch: T;

		public clear() {
			this._listeners = null;
			this._addQueue = null;
		}

		private static EMIT(this: NewSignal<any>, ...args: Array<any>): void {
			if (!this._listeners) {
				return;
			}
			this._inRun = true;
			try {
				for (let i = 0; i < this._listeners.length; i++) {
					let node = this._listeners[i];
					if (!node) {
						continue;
					}
					let listener = node.listener;
					listener.apply(null, args);
				}
			} finally {
				this._inRun = false;
				let j = 0;
				for (let i = 0; i < this._listeners.length; i++) {
					let node = this._listeners[i];
					if (node) {
						this._listeners[j] = node;
						j++;
					}
				}
				this._listeners.length = j;
				let queue = this._addQueue;
				if (queue) {
					this._addQueue = null;
					for (let i = 0; i < queue.length; i++) {
						let node = queue[i];
						if (node) {
							this.addNode(node);
						}
					}
				}
			}
		}

		public add(listener: T, priority?: number): T {
			return this.addListener(listener, priority);
		}

		public addOnce(listener: T, priority: number = 0): T {
			return this.addListenerOnce(listener, priority);
		}

		public hasListeners(): boolean {
			if (this._listeners) {
				for (let node of this._listeners) {
					if (node) {
						return true;
					}
				}
			}
			if (this._addQueue) {
				for (let node of this._addQueue) {
					if (node) {
						return true;
					}
				}
			}
			return false;
		}

		public numListeners(): number {
			if (!this._listeners && !this._addQueue) {
				return 0;
			}
			let set = new Set();
			if (this._listeners) {
				for (let node of this._listeners) {
					if (node) {
						set.add(node);
					}
				}
			}
			if (this._addQueue) {
				for (let node of this._addQueue) {
					if (node) {
						set.add(node);
					}
				}
			}
			return set.size;
		}
	}

	class SignalNode<T> {
		constructor(listener: T, priority: number) {
			this.listener = listener;
			this.priority = priority;
		}

		public readonly listener: T;
		public readonly priority: number;
	}
	
	NewSignal.prototype.emit = NewSignal['EMIT'];
	NewSignal.prototype.dispatch = NewSignal['EMIT'];
}
