///ts:ref=Console
/// <reference path="../console/Console.ts"/> ///ts:ref:generated
namespace panda {
	export namespace GlobalInstanceChecker {
		export type ItemType = { getIsValid: () => boolean, getName: () => string, uniqId: number };
		var set: Set<ItemType> = new Set();

		export function watch(item: ItemType) {
			set.add(item);
		}

		export function unWatch(item: ItemType) {
			set.delete(item);
		}

		export function check(): void {
			set.forEach(item => {
				if (item && item.getIsValid()) {
					Console.errorUncritical(new AdvancedError('Valid leaked iterator ' + item.getName()));
				}
			});
			set = new Set();
		}
	}
}