	function insertWeakSet() {
		var all = new WeakMap();
		var proto = WeakSet.prototype;
		proto.add = function (value: any) {
			return all.get(this).set(value, 1), this;
		};
		proto.delete = function (value: any) {
			return all.get(this).delete(value);
		};
		proto.has = function (value: any) {
			return all.get(this).has(value);
		};
		(window as any)['WeakSet'] = WeakSet;
		function WeakSet(iterable: any) {
			'use strict';
			all.set(this, new WeakMap);
			if (iterable)
				iterable.forEach(this.add, this);
		}
	}

	function initSocialInfoDefault(): void {
		interface SocialStatInfo {
			project: string,
			site: string,
			uid: string
		}
		interface Social {
			config: {
				stat: SocialStatInfo
			}
		}

		let social = (<any>window).social as Social;
		if (!social || !social.config || !social.config.stat) {
			return;
		}
		let statInfo = social.config.stat;
		let info = PlatformDescriber.getInfo();
		info.sns = statInfo.site;
		info.userId = statInfo.uid;
		info.project = statInfo.project;
	}
