namespace panda {
	export class Stat {
		public static intance: Stat = null;

		constructor(url: panda.url.Url, collectTimeout: number) {
			this._url = url;
			this._collectTimeout = collectTimeout;
			this._loader.onComplete.addListener((loader, responce, wasError: boolean) => {
				this.onSended.emit(wasError);
			});
		}

		private _url: panda.url.Url;
		private _collectTimeout: number;
		private _queue: Array<StatEvent> = new Array();
		private _timeout: any = NaN;
		private _loader: URLLoader = new URLLoader();

		public send(event: StatEvent): void {
			if (BuildInfo.online !== 'true') {
				return;
			}
			Console.here;
			if (!event.hasUniq('type')) {
				Console.errorUncritical(new AdvancedError('event has no type'));
			}
			if (!event.hasUniq('project')) {
				Console.errorUncritical(new AdvancedError('event has no project, type: ' + event.$getData()['type']));
			}
			if (!event.hasUniq('site')) {
				Console.errorUncritical(new AdvancedError('event has no site, type: ' + event.$getData()['type']));
			}
			//PandaConsole.logDebug('PandaStat::send', 'event.type', event.$getData()['type']);
			this._queue.push(event);
			this.scheduleSend();
		}

		public onSended = new Signal<(wasError: boolean) => void>();

		private scheduleSend(): void {
			//PandaConsole.logDebug('PandaStat::scheduleSend', 'timeout', this._timeout);
			if (isNaN(this._timeout)) {
				this._timeout = setTimeout(this.handler_collectTimeOutEnded, this._collectTimeout);
			}
		}

		private handler_collectTimeOutEnded = () => {
			//PandaConsole.logDebug('PandaStat::collectTimeOutEnded', 'timeout', this._timeout);
			this._timeout = NaN;
			if (this._loader.getLoading()) {
				//PandaConsole.logDebug('PandaStat::collectTimeOutEnded', 'this._loader.loading');
				this._loader.onComplete.addListener(this.sendToServer);
			} else {
				//PandaConsole.logDebug('PandaStat::collectTimeOutEnded', 'sendToServer');
				this.sendToServer();
			}
		}

		private sendToServer = () => {
			Console.logDebug('PandaStat::sendToServer');
			this._loader.onComplete.removeListener(this.sendToServer);
			let data: string = this.stringifyEvents();
			//PandaConsole.logInfo(JSON.stringify(this._queue.map(e=>e.$getData()), null, 4));
			this._queue.length = 0;
			let req = new URLRequest(this._url, 'POST', data);
			req.numRetriesToDo = 0; // for stat retries is bad idea
			this._loader.load(req);
		}

		private stringifyEvents(): string {
			return JSON.stringify(this._queue.map(e => e.$getData()));
		}
	}
}
