namespace panda {
	export function safeBind<Ret>(func: () => Ret, thisArg: any): () => Ret;

	export function safeBind<Ret, P1>(func: (p1: P1) => Ret, thisArg: any): (p1: P1) => Ret;
	export function safeBind<Ret, P1>(func: (p1: P1) => Ret, thisArg: any, p1: P1): () => Ret;

	export function safeBind<Ret, P1, P2>(func: (p1: P1, p2: P2) => Ret, thisArg: any): (p1: P1, p2: P2) => Ret;
	export function safeBind<Ret, P1, P2>(func: (p1: P1, p2: P2) => Ret, thisArg: any, p1: P1): (p2: P2) => Ret;
	export function safeBind<Ret, P1, P2>(func: (p1: P1, p2: P2) => Ret, thisArg: any, p1: P1, p2: P2): () => Ret;

	export function safeBind<Ret, P1, P2, P3>(func: (p1: P1, p2: P2, p3: P3) => Ret, thisArg: any): (p1: P1, p2: P2, p3: P3) => Ret;
	export function safeBind<Ret, P1, P2, P3>(func: (p1: P1, p2: P2, p3: P3) => Ret, thisArg: any, p1: P1): (p2: P2, p3: P3) => Ret;
	export function safeBind<Ret, P1, P2, P3>(func: (p1: P1, p2: P2, p3: P3) => Ret, thisArg: any, p1: P1, p2: P2): (p3: P3) => Ret;
	export function safeBind<Ret, P1, P2, P3>(func: (p1: P1, p2: P2, p3: P3) => Ret, thisArg: any, p1: P1, p2: P2, p3: P3): () => Ret;

	export function safeBind<Ret, P1, P2, P3, P4>(func: (p1: P1, p2: P2, p3: P3, p4: P4) => Ret, thisArg: any): (p1: P1, p2: P2, p3: P3, p4: P4) => Ret;
	export function safeBind<Ret, P1, P2, P3, P4>(func: (p1: P1, p2: P2, p3: P3, p4: P4) => Ret, thisArg: any, p1: P1): (p2: P2, p3: P3, p4: P4) => Ret;
	export function safeBind<Ret, P1, P2, P3, P4>(func: (p1: P1, p2: P2, p3: P3, p4: P4) => Ret, thisArg: any, p1: P1, p2: P2): (p3: P3, p4: P4) => Ret;
	export function safeBind<Ret, P1, P2, P3, P4>(func: (p1: P1, p2: P2, p3: P3, p4: P4) => Ret, thisArg: any, p1: P1, p2: P2, p3: P3): (p4: P4) => Ret;
	export function safeBind<Ret, P1, P2, P3, P4>(func: (p1: P1, p2: P2, p3: P3, p4: P4) => Ret, thisArg: any, p1: P1, p2: P2, p3: P3, p4: P4): () => Ret;

	export function safeBind<Ret, P1, P2, P3, P4, P5>(func: (p1: P1, p2: P2, p3: P3, p4: P4, p5: P5) => Ret, thisArg: any): (p1: P1, p2: P2, p3: P3, p4: P4, p5: P5) => Ret;
	export function safeBind<Ret, P1, P2, P3, P4, P5>(func: (p1: P1, p2: P2, p3: P3, p4: P4, p5: P5) => Ret, thisArg: any, p1: P1): (p2: P2, p3: P3, p4: P4, p5: P5) => Ret;
	export function safeBind<Ret, P1, P2, P3, P4, P5>(func: (p1: P1, p2: P2, p3: P3, p4: P4, p5: P5) => Ret, thisArg: any, p1: P1, p2: P2): (p3: P3, p4: P4, p5: P5) => Ret;
	export function safeBind<Ret, P1, P2, P3, P4, P5>(func: (p1: P1, p2: P2, p3: P3, p4: P4, p5: P5) => Ret, thisArg: any, p1: P1, p2: P2, p3: P3): (p4: P4, p5: P5) => Ret;
	export function safeBind<Ret, P1, P2, P3, P4, P5>(func: (p1: P1, p2: P2, p3: P3, p4: P4, p5: P5) => Ret, thisArg: any, p1: P1, p2: P2, p3: P3, p4: P4): (p5: P5) => Ret;
	export function safeBind<Ret, P1, P2, P3, P4, P5>(func: (p1: P1, p2: P2, p3: P3, p4: P4, p5: P5) => Ret, thisArg: any, p1: P1, p2: P2, p3: P3, p4: P4, p5: P5): () => Ret;

	export function safeBind(func: Function, thisArg: any, ...argArray: any[]): any;
	export function safeBind(func: Function, thisArg: any, ...argArray: any[]): any {
		return function (...externalArgs: any[]): any {
			return func.apply(thisArg, argArray.concat(externalArgs));
		}
	}
}