			export function fillBrowserProperties(result: ParseResult): void {
				Console.here;
				let testers: Array<(result: ParseResult) => boolean> = [
					tryChrome, //   ,     
					tryEdge,
					tryFirefox,
					tryOldOpera,
					tryNewOpera,
					tryYandex,
					tryFacebook,
					tryIE,
					trySafari,
					tryAir,
					tryChromeIos,
					tryFirefoxIos,
					tryPaleMoon,
					tryAol,
					tryOneProductGoogleExtender,
					tryOneProductFirefoxExtender,
				];
				for (let func of testers) {
					let stopTrying: boolean = func(result);
					if (stopTrying) {
						break;
					}
					if (result.parsedCorretly) {
						break;
					}
				}
			}

			export function tryOldOpera(result: ParseResult): boolean {
				let opera: Extended.Product = result.getFirstProductByName(ProductName.Opera);
				if (!opera) {
					return false;
				}
				let version: Extended.Product = result.getFirstProductByName(ProductName.Version);
				if (!version) {
					Console.errorUncritical(new AdvancedError('Old Opera without version', 130));
					return false;
				}
				result.browser = opera.browser;
				result.version = version.version;
				result.versionNumber = version.versionNumber;
				result.parentBrowser = opera.browser;
				result.parentVersion = version.version;
				result.parentVersionNumber = version.versionNumber;
				if (result.products.length === 3) {
					result.parsedCorretly = true;
				}
				return true;
			}

			export function tryChrome(result: ParseResult): boolean {
				let chrome: Extended.Product = result.getFirstProductByName(ProductName.Chrome);
				if (!chrome) {
					return false;
				}
				let edge: Extended.Product = result.getFirstProductByName(ProductName.Edge);
				if (edge) {
					return false;
				}
				let mozilla: Extended.Product = result.getFirstProductByName(ProductName.Mozilla);
				if (!mozilla) {
					Console.errorUncritical(new AdvancedError('Chrome without Mozilla', 131));
					return false;
				}
				if (mozilla.versionNumber !== 5) {
					Console.errorUncritical(new AdvancedError('Chrome with mozilla version !==5', 132));
					return false;
				}
				let webKit: Extended.Product = result.getFirstProductByName(ProductName.AppleWebKit);
				if (!webKit) {
					Console.errorUncritical(new AdvancedError('Chrome without AppleWebKit', 133));
					return false;
				}
				let safari: Extended.Product = result.getFirstProductByName(ProductName.Safari);
				if (!safari) {
					Console.errorUncritical(new AdvancedError('Chrome without Safari', 134));
					return false;
				}

				result.browser = chrome.browser;
				result.version = chrome.version;
				result.versionNumber = chrome.versionNumber;
				result.parentBrowser = chrome.browser;
				result.parentVersion = chrome.version;
				result.parentVersionNumber = chrome.versionNumber;

				if (result.products.length === 4) {
					result.parsedCorretly = true;
				}

				return false;
			}

