
namespace panda {

	type TimerGram = { func: () => void, time: number, id: number, args: Array<any> };
	export class TimerSystem {
		private static instance: TimerSystem = null;
		public static init(): void {
			if (this.instance) {
				throw new AdvancedError('double TimerSystem.init()');
			}
			this.instance = new TimerSystem();
			panda.MainLoop.onEnterFrame.addListener(()=>{
				let now = performance.now();
				this.instance.tick(now);
			}, 10);
			setInterval(() => {
				let now = performance.now();
				let delta = now - this.instance._lastTickTime;
				if (delta > 450) {
					this.instance.tick(now);
				}
			}, 500);
		}

		private numTimers: number = 0;

		public constructor() {
			(window as any)['setTimeout'] = (): number => {
				this.numTimers++;
				// @ts-ignore
				let rawArgs = arguments;

				let rawArgsLength = rawArgs.length;
				let gram: TimerGram;
				if (rawArgsLength == 2) {
					gram = this.getGram(rawArgs[0], rawArgs[1], null);
				} else if (rawArgsLength == 1) {
					gram = this.getGram(rawArgs[0], 0, null);
				} else {
					let restArgs: Array<any> = [];
					for (let i = 2; i < rawArgsLength; i++) {
						restArgs[i - 2] = rawArgs[i];
					}
					gram = this.getGram(rawArgs[0], rawArgs[1], restArgs);
				}

				if (this._inTick) {
					this._callbackArrayTail.push(gram);
				} else {
					this._callbackArray.push(gram);
				}
				return gram.id;
			}
			(window as any)['clearTimeout'] = (timeoutID: number): void => {
				this._skipSet.add(timeoutID);
			}
		}

		private _callbackArray: Array<TimerGram> = [];
		private _callbackArrayTail: Array<TimerGram> = [];
		private _skipSet: Set<number> = new Set();
		private _inTick: boolean = false;

		private _lastTickTime: number = 0;
		private _objPool: Array<TimerGram> = [];
		private _idCounter = 1000;

		private getGram(func: () => void, time: number, restArgs: Array<any>): TimerGram {
			let gram: TimerGram;
			if (this._objPool.length) {
				gram = this._objPool.pop();
			} else {
				gram = Object.create(null);
			}
			gram.func = func;
			gram.time = time + this._lastTickTime;
			gram.id = this._idCounter++;
			gram.args = restArgs;
			return gram;
		}

		private poolGram(gram: TimerGram): void {
			this.clearGram(gram);
			this._objPool.push(gram);
		}

		private clearGram(gram: TimerGram): void {
			gram.func = null;
			gram.time = 0;
			gram.id = 0;
			gram.args = null;
		}

		private tick(millisecondsAbsolute: number): void {
			this._inTick = true;
			this._lastTickTime = millisecondsAbsolute;

			let j = 0;
			let callbackArray = this._callbackArray;

			for (let i = 0; i < callbackArray.length; i++) {
				let gram = callbackArray[i];

				let skip = this._skipSet.has(gram.id);
				let call = gram.time < millisecondsAbsolute;

				if (skip) {
					this.poolGram(gram);
				} else if (call) {
					gram.func.apply(window, gram.args);
					this.poolGram(gram);
				} else {
					callbackArray[j] = gram;
					j++;
				}
			}
			callbackArray.length = j;

			for (let gram of this._callbackArrayTail) {
				callbackArray.push(gram);
			}
			this._callbackArrayTail.length = 0;

			this._skipSet.clear();


			// if (((millisecondsAbsolute % 10) | 0) == 0) {
			//     PandaConsole.logDebug(`numTimers: ${this.numTimers}`);
			// }

			this.numTimers = 0;
			this._inTick = false;
		}
	}
}
