namespace panda {
	export namespace PerfMonitor {
		export function init() {
			MainLoop.onEnterFrame.addListener(handler_enterFrame);
			_container = document.createElement('div');
			document.getElementsByTagName('body').item(0).appendChild(_container);
			let style: any = _container.style;
			style['position'] = 'fixed';
			style['font-size'] = '10px';
			style['color'] = '#000000';
			style['background-color'] = 'lightblue';
			style['font-weight'] = 'bold';
			style['top'] = '0px';



			style['-webkit-touch-callout'] = 'none'; /* iOS Safari */
			style['-webkit-user-select: none'] = 'none'; /* Safari */
			style['-khtml-user-select'] = 'none'; /* Konqueror HTML */
			style['-moz-user-select: none'] = 'none'; /* Firefox */
			style['-ms-user-select'] = 'none'; /* Internet Explorer/Edge */
			style['user-select'] = 'none'; /* Non-prefixed version, currently supported by Chrome and Opera */
			style['display'] = 'block';
			style['display'] = 'none';

			HTMLUtils.setTextContent(_container, 'FPS');
		}
		var _container: HTMLDivElement;

		var _framesCounter: number = 0;
		const _framesDeltas: Array<number> = new Array<number>();
		const _codeDetails: Array<number> = new Array<number>();
		const measuringCount: number = 1 * 60 * 60; // one minute on 60FPS

		function handler_enterFrame(timeCoef: number, now: number, timeDelta: number): void {
			_framesCounter++;

			let fps = 1000 / timeDelta;
			updateCounter(fps);
			let deltas: Array<number> = _framesDeltas;
			deltas.push(timeDelta);
			if (deltas.length > measuringCount) {
				deltas.shift();
			}
		}

		var _introText: string = null;
		var _lasFpsUpdate: number = 0;
		function updateCounter(fps: number): void {
			let delta = performance.now() - _lasFpsUpdate;
			if (delta >= 500) {
				if (!_introText) {
					let date = new Date(parseInt(BuildInfo.buildDate));;
					_introText = 'Build: ' + BuildInfo.buildNumber + ', ' + date.toLocaleString() + ' FPS: ';
				}
				let framesDeltas = _framesDeltas;
				let start = framesDeltas.length - 30;
				if (start < 0) {
					start = 0;
				}
				let avgFps = (1000 / ArrayUtils.avg(framesDeltas, start)).toFixed(2);
				HTMLUtils.setTextContent(_container, _introText + fps.toFixed(1) + ' AVG: ' + avgFps);
				_lasFpsUpdate = performance.now();
			}
		}

		export function enterFrameDispatchingTime(timeDelta: number): void {
			_codeDetails.push(timeDelta);
			if (_codeDetails.length > measuringCount) {
				_codeDetails.shift();
			}

			let framesDeltas: Array<number> = _framesDeltas;
			if (_framesCounter >= measuringCount) {
				let avgFps = (1000 / ArrayUtils.avg(framesDeltas)).toFixed(2);
				let minFps = (1000 / ArrayUtils.max(framesDeltas)).toFixed(2);
				let medFps = (1000 / ArrayUtils.percentile(framesDeltas, 0.5)).toFixed(2);

				let avgTime = ArrayUtils.avg(_codeDetails).toFixed(2);
				let maxTime = ArrayUtils.max(_codeDetails).toFixed(2);
				let medTime = ArrayUtils.percentile(_codeDetails, 0.5).toFixed(2);

				Console.logDebug('AvgFps:', avgFps, 'MedFps:', medFps, 'MinFps:', minFps, 'AvgCodeMs:', avgTime, 'MaxCodeMs:', maxTime, 'MedCodeMs:', medTime);
				Console.logDebug(JSON.stringify(PerfUtils.storage));
				_framesCounter = 0;
				PerfUtils.storage = {};
			}
		}

		export function toggleMonitor(to?:'on'|'off') {
			let style = _container.style;
			if (!to) {
				if (style.display == 'block') {
					to = 'off';
				} else {
					to = 'on';
				}
			}

			if (to == 'on') {
				style.display = 'block';
			}
			if (to == 'off') {
				style.display = 'none';
			}

		}
	}
}
