namespace panda {
	export namespace FlogsSenderPrivate {
		export class FlogsSender {
			public constructor(project:string = null, flogs2Project:string = null) {
				this._project = project;
				this._flogs2Project = flogs2Project;
				if (this._project.indexOf('NaszaKlasa') >= 0 || this._flogs2Project.indexOf('NaszaKlasa') >= 0) {
					this.disabled = true;
				}
				
				this.createChunk();
				this._currenctChunk.add(true, 0, ConsoleMessageLevels.INFO, PlatformDescriber.getInfo().toString(), []);
				this._startTimestamp = DateUtils.now();
			}

			private _logChuncks = new Array<Chunk>();
			private _currenctChunk: Chunk;
			private _startTimestamp: number;
			private _sendSheduled: boolean = false;
			private _errorMessage: string = null;
			private _counter: number = 0;
			private _project:string = null;
			private _flogs2Project:string = null;
			public  disabled:boolean = false;

			private createChunk(): void {
				this._currenctChunk = new Chunk();
				this._currenctChunk.onFull.addListener(this.handler_chunkFull);
				this._logChuncks.push(this._currenctChunk);
			}

			public handler_log = (level: ConsoleMessageLevel, message: string, args: Array<any>) => {
				if (this.disabled) {
					return;
				}
				let marker: boolean = level === ConsoleMessageLevels.WARN || level === ConsoleMessageLevels.ERROR;
				this._currenctChunk.add(marker, DateUtils.now() - this._startTimestamp, level, message, args);
				let fullMessage = [message].concat(args).join(' ');
				
				if (marker && !this._sendSheduled && this._counter < 10) {
					if (fullMessage.indexOf('out of memory') >= 0) {
						let anyWindow = window as any;
						if (anyWindow.showNoWebGL) {
							anyWindow.showNoWebGL('Out_Of_Memory_text_match')
							return;
						}
					}
					Console.logDebug('send scheduled');
					setTimeout(this.handler_send); //   nextFrameCall,     ,    
					this._sendSheduled = true;
					this._errorMessage = fullMessage;
					this._counter++;
				}
			}

			private handler_chunkFull = () => {
				this._currenctChunk.onFull.removeListener(this.handler_chunkFull);
				this.createChunk();
			}

			private handler_send = () => {
				if (this.disabled) {
					return;
				}
				let info = PlatformDescriber.getInfo();
				this.sendTo('flogs.crazypanda.ru', this._project || info.project + '_JS');
				if (this._flogs2Project) {
					this.sendTo('flogs2.crazypanda.ru', this._flogs2Project);
				}
				Console.logDebug('sent');
				this._sendSheduled = false;
				this._errorMessage = null;
			}
			
			private sendTo(url:string, projectName:string) {
				if ((window as any).noWebGlDetected) {
					return;
				}
				let uri = new Url({
					hostname: url,
					pathname: 'addlog',
					query: { 'rand': Math.random() }
				});
				let data: URLVariables = new URLVariables();
				let info = PlatformDescriber.getInfo();
				data.data['project'] = projectName;
				data.data['user_id'] = info.userId;
				data.data['client_id'] = info.browserInfo.browser + ' ' + info.browserInfo.version;
				data.data['username'] = info.username;
				data.data['type'] = this._errorMessage;
				data.data['log'] = this.getStringified();
				let dataString = data.toString();				
				let req = new URLRequest(uri, 'POST');
				req.data = dataString;
				req.requestHeaders = new Array<URLRequestHeader>();
				req.requestHeaders.push(new URLRequestHeader('Content-Type', 'application/x-www-form-urlencoded'));
				let loader = new URLLoader(req);
				loader.load();
			}

			public getStringified(): string {
				return this._logChuncks.join('...\nskipped\n...\n');
			}
		}

		export class Chunk {

			public constructor(contextSize: number = 50) {
				this._contextSize = contextSize;
			}

			public onFull = new Signal<() => void>();
			private _items = new List<string>('FlogsSenderChunk');
			private _lastMarkerIndex: number = 0;
			private _contextSize: number;

			public add(marker: boolean, time: number, level: ConsoleMessageLevel, message: string, args: Array<any>): void {
				let stringified = time + ' ' + level + ', ' + message;
				if (args.length) {
					stringified += ', ' + args.join(', ');
				}
				stringified += '\n';

				let items = this._items;
				items.insert(stringified);
				if (marker) {
					this._lastMarkerIndex = items.getLength();
				}

				if (this._lastMarkerIndex === 0) {
					if (items.getLength() - this._lastMarkerIndex > this._contextSize) {
						items.remove(items.getHead());
					}
				} else {
					if (items.getLength() - this._lastMarkerIndex >= this._contextSize) {
						this.onFull.emit();
					}
				}
			}

			public toString(): string {
				let stringified: string = '';
				let iterator = this._items.getHead();
				while (!iterator.getEnded()) {
					stringified += iterator.getValue();
					iterator.next();
				}
				return stringified;
			}
		}
	}

	export import FlogsSender = FlogsSenderPrivate.FlogsSender;
}
