		//const funcNameRegex:RegExp = /function ([^\( ]+?)\(/;
		const funcNameRegex: RegExp = /function ([^\s\.]*?) ?\(/;
		const classNameRegex: RegExp = /class ([^\s\.]*?)\s+/;
		const objNameRegex: RegExp = /\[object (.+)\]/;

