namespace panda {
	export type ConsoleMessageLevel = "Warn" | "Error" | "Debug" | "Info" | "External";

	export class ConsoleMessageLevels {
		public static WARN: ConsoleMessageLevel = "Warn";
		public static ERROR: ConsoleMessageLevel = "Error";
		public static DEBUG: ConsoleMessageLevel = "Debug";
		public static INFO: ConsoleMessageLevel = "Info";
		public static EXTERNAL: ConsoleMessageLevel = "External";
	}


	export class ConsoleMessageStyles {
		public static Warn: string = "background: #000000; color: #00ff5c;";
		public static Error: string = "background: #000000; color: #ff0000;";
		public static Debug: string = "background: #5b86ff; color: #000000;";
		public static INFO: string = "background: #5b86ff; color: #000000;";
		public static EXTERNAL: string = "background: #5b86ff; color: #000000;";

		public static getStyle(level: ConsoleMessageLevel): string {
			let style: string = (<any>ConsoleMessageStyles)[level];
			return style;
		}

		public static hasStyle(level: ConsoleMessageLevel): boolean {
			let style: string = (<any>ConsoleMessageStyles)[level];
			return !!style;
		}
	}
}
